@font-face {
  font-family: main-font;
  src: url("Orbitron-ExtraBold.c67f8c93.ttf");
}

@font-face {
  font-family: code-font;
  src: url("RobotoMono-Bold.05c1ae7c.ttf");
}

canvas {
  position: fixed;
  top: 0;
  left: 0;
}

main {
  position: absolute;
}

.progress-bar-container {
  background-color: #306b6d;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  font-family: main-font;
  display: flex;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

#progress-bar {
  width: 30%;
  height: 2%;
  margin-top: .5%;
}

label {
  color: #fff;
  font-size: 2rem;
}

.nav-panel {
  flex-direction: column;
  align-items: center;
  display: none;
  position: fixed;
  right: 2vw;
}

.nav-panel button:hover {
  color: #68f8d9;
  text-shadow: 1px 1px 10px #68f8d9, 1px 1px 10px #fff;
  cursor: pointer;
}

.nav-panel button {
  color: #fff;
  background: none;
  border: none;
  margin-top: 20px;
  font-family: main-font;
  font-size: 20px;
}

.section {
  color: #fff;
  background-color: #436d94;
  border: 1px groove #fff;
  border-radius: 5px;
  flex-direction: column;
  align-items: center;
  width: 300px;
  height: 400px;
  display: none;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.section * {
  margin-top: 20px;
  font-family: code-font;
}

@media (width <= 800px) {
  .nav-panel {
    flex-direction: row;
    justify-content: center;
    width: 100%;
    bottom: 2vh;
  }

  .section {
    width: 150px;
    height: 200px;
    font-size: 12px;
  }

  .section * {
    margin-top: 10px;
  }

  .nav-panel button {
    font-size: 10px;
  }
}

